/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 200px;
    height: 44px;
    margin: 16px 36px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #57417C;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 32px;
    width: 32px;
    left: 8px;
    bottom: 6px;
    background-color: white;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #57417C;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #57417C;;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(152px);
    transform: translateX(152px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
